*{
  margin: 0;
  padding: 0;
  box-sizing: border-box;
}

.App{
  display: flex;
  align-items: center;
  justify-content: center;
  flex-direction: column;
}
a{
  color:inherit;
  text-decoration: none;
}
.cards{
  display: flex;
  flex-direction: row;
  flex-wrap: wrap;
  max-width: 420px;
  width: 100%;
  margin: 0 auto;
}
.card{
  width: 100%;
  display: flex;
  gap:12px;
}
.poster{
  width: 100%;
  max-width: 160px;
  height: 150px;
  border-radius: 24px;
  overflow: clip;
}

.poster img{
  width: 100%;
  height: 100%;
  object-fit: cover;
  object-position: center;
}

.titles{
  display: flex;
  flex-direction: column;
  gap:6px;
}

.title{
  font-size: 1.2rem;
  width: calc(420px - 196px);
  white-space: nowrap;
  overflow: hidden;
  text-overflow: ellipsis;
  line-height: 1.2rem;
}

.sub_title{
  font-size: 1rem;
  line-height: 1rem;
  opacity: 0.8;
}
.buttons{
  width: 100%;
  display: flex;
  justify-content: space-around;
  font-weight: 800;
  font-size: 0.6rem;
}